















































import Vue from "vue";
import InputNumber from "@ca/InputNumber/index.vue";
import LabelText from "@ca/LabelText/index.vue";
import MinusText from "@ca/MinusText/index.vue";
import EqualText from "@ca/EqualText/index.vue";

import {
  GET_REVENUE,
  GET_EXPENSE,
  GET_DIRECTORS_COMPENSATION,
  GET_PROFIT,
} from "@/store/Corporation/getters";

import {
  SET_REVENUE,
  SET_EXPENSE,
  SET_DIRECTORS_COMPENSATION,
  SET_PROFIT,
} from "@/store/Corporation/mutations";
//import handleCanvas from "@/mixin/handleCanvas";
import { mapGetters } from "vuex";
export default Vue.extend({
  components: {
    InputNumber,
    LabelText,
    MinusText,
    EqualText,
  },
  computed: {
    setRevenue() {
      return SET_REVENUE;
    },
    setExpense() {
      return SET_EXPENSE;
    },
    setDirectorsCompensation() {
      return SET_DIRECTORS_COMPENSATION;
    },
    setProfit() {
      return SET_PROFIT;
    },
    ...mapGetters({
      getRevenue: GET_REVENUE,
      getExpense: GET_EXPENSE,
      getDirectorsCompensation: GET_DIRECTORS_COMPENSATION,
      getProfit: GET_PROFIT,
    }),
  },
  props: {
    label: String,
    type: String,
    items: [],
  },
});
